/*
|-------------------------------------------------------------------------------
| #reboot
|-------------------------------------------------------------------------------
|
|
*/
*,
*:before,
*:after {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

body {
  font-family: "Helvetica Neue", "Hiragino Sans GB", sans-serif;
  font-size: 14px;
  line-height: 1.5;
  color: #555;
  background-color: #f4f4f4;
  word-wrap: break-word;
}

/* Heading
---------------------------------*/
h1, h2, h3, h4, h5, h6 {
  color: #212121;
  font-weight: 500;
  line-height: 1.2;
  margin: 0 0 10px 0;
}

h1 small, h2 small, h3 small, h4 small, h5 small, h6 small {
  color: #616161;
}

h1 {
  font-size: 36px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 24px;
}

h4 {
  font-size: 18px;
}

h5 {
  font-size: 14px;
}

h6 {
  font-size: 12px;
}

/* Paragraphs
---------------------------------*/
p {
  margin-top: 0;
  margin-bottom: 10px;
}

ins,
mark {
  background: #ffa;
}

hr {
  display: block;
  padding: 0;
  margin: 30px 0;
  height: 0;
  border: 0;
  border-top: 1px solid #eee;
  box-sizing: content-box;
}

/* Link
---------------------------------*/
a {
  color: #7266BA;
  text-decoration: none;
}

/* List
---------------------------------*/
ul, ol {
  margin-top: 0;
  margin-bottom: 10px;
}

ul ul, ul ol, ol ul, ol ol {
  margin-bottom: 5px;
}

dl dt {
  font-weight: bold;
}

dl dd {
  margin: 0 0 15px 0;
}

/* Blockquote
---------------------------------*/
blockquote {
  border-left: 2px solid #ddd;
  padding: .5rem .25rem;
}

blockquote cite {
  color: #616161;
}

blockquote > p:first-child {
  margin-top: 0;
}

blockquote > p:last-child {
  margin-bottom: 0;
}

/* Code
---------------------------------*/
code, pre {
  -moz-osx-font-smoothing: auto;
  -webkit-font-smoothing: auto;
  font-family: "Source Code Pro", "Monaco", "Inconsolata", monospace;
  line-height: 1.25;
}

pre {
  margin-top: 0;
  margin-bottom: 10px;
  padding: 16px 20px;
  background: #f5f7fa;
  color: #69707a;
  white-space: pre;
  word-wrap: normal;
}

code {
  background: #f5f7fa;
  color: #ed6c63;
  font-size: 12px;
  font-weight: normal;
  padding: 1px 3px 3px;
}

pre code {
  background: #f5f7fa;
  color: #69707a;
  display: block;
  overflow-x: auto;
}

/* Table
---------------------------------*/
caption {
  text-align: left;
  caption-side: top;
}

th {
  text-align: left;
}

/* Figures
---------------------------------*/
figure {
  margin: 0 0 10px;
}

img {
  vertical-align: middle;
}

/* Form
---------------------------------*/
fieldset {
  border: 0;
  padding: 0;
  margin: 0;
  min-width: 0;
}

legend {
  display: block;
  width: 100%;
  padding: 0;
  margin: 0 0 5px 0;
}

label {
  display: inline-block;
  margin: 0 0 5px 0;
}

textarea {
  resize: vertical;
}

/* Misc
---------------------------------*/
a,
area,
button,
[role="button"],
input,
label,
select,
summary,
textarea {
  touch-action: manipulation;
}

[role=button] {
  cursor: pointer;
}

[hidden] {
  display: none !important;
}

[disabled],
.is-disabled {
  cursor: not-allowed;
}

/*
|-------------------------------------------------------------------------------
| #App layout
|-------------------------------------------------------------------------------
|
|
*/
html,
body {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.l-app {
  position: relative;
  width: 100%;
  height: 100%;
}

.is-modal-open .l-main,
.is-alert-open .l-main,
.is-notify-open .l-main {
  overflow: hidden;
}

.is-modal-open .l-app,
.is-alert-open .l-app {
  filter: blur(12px);
}

.l-main {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}

.l-main__inner {
  order: 2;
  max-width: 640px;
  width: 100%;
  min-height: 100vh;
}

.l-main__left {
  order: 1;
  flex: 1;
  display: flex;
  overflow: hidden;
}

.l-main__right {
  order: 3;
  flex: 1;
  overflow: hidden;
}

/*
