/*
|-------------------------------------------------------------------------------
| #Level
|-------------------------------------------------------------------------------
|
|
*/
.l-level {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.l-level.is-top {
  align-items: flex-start;
}

.l-level.is-bottom {
  align-items: flex-end;
}

.l-level > .l-level__item {
  flex: 1;
  text-align: center;
}

.l-level__left {
  order: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.l-level__left.is-top {
  align-items: flex-start;
}

.l-level__left.is-bottom {
  align-items: flex-end;
}

.l-level__left + .l-level__right {
  padding-left: 8px;
}

.l-level__right {
  order: 2;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.l-level__right.is-top {
  align-items: flex-start;
}

.l-level__right.is-bottom {
  align-items: flex-end;
}

.l-level__right + .l-level__left {
  padding-right: 8px;
}

.l-level__left.is-expaned,
.l-level__right.is-expaned {
  flex: 1;
  overflow: hidden;
}

.l-level__left.is-expaned .l-level__item.is-expaned,
.l-level__right.is-expaned .l-level__item.is-expaned {
  word-wrap: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.l-level__left .l-level__item:nth-child(n+2),
.l-level__right .l-level__item:nth-child(n+2) {
  margin-left: 8px;
}

.l-level__item {
  white-space: nowrap;
}

/*
|-------------------------------------------------------------------------------
| #Content layouts
|-------------------------------------------------------------------------------
|
|
*/
/* Container
---------------------------------*/
.l-container {
  margin-left: 8px;
  margin-right: 8px;
}

/* Content
---------------------------------*/
.l-content {
  margin-left: 15px;
  margin-right: 15px;
}

/* Group
---------------------------------*/
.l-group {
  margin: 0 15px 30px 15px;
}

.l-group__title {
  position: relative;
  display: block;
  color: #424242;
  font-size: 14px;
  line-height: 1.2;
  margin: 0 15px 5px 15px;
}

.l-group__text {
  position: relative;
  display: block;
  margin: -30px 15px 30px 15px;
  padding-top: 5px;
}

/*
|-------------------------------------------------------------------------------
| #Buttons
|-------------------------------------------------------------------------------
|
|
*/
.btn {
  position: relative;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 14px;
  line-height: 20px;
  padding: 6px 12px;
  border-radius: 3px;
  color: #555;
  background-color: #fff;
  border: 0;
  outline: none;
  white-space: nowrap;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}

.btn:active, .btn.is-active {
  background-color: #f3f7f9;
}

.btn.is-loading {
  color: transparent;
  cursor: not-allowed;
}

.btn.is-loading::before {
  animation: spin-around 500ms infinite linear;
  border: 2px solid #616161;
  border-radius: 8px;
  border-right-color: transparent;
  border-top-color: transparent;
  content: "";
  display: block;
  height: 16px;
  width: 16px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -8px;
  margin-top: -8px;
}

/* Modifiers
---------------------------------*/
.btn--outline::after {
  content: '';
  position: absolute;
  pointer-events: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 0 1px #ddd;
  border-radius: 3px;
  transform-origin: left top;
  box-sizing: border-box;
  z-index: 200;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
  .btn--outline::after {
    width: 200%;
    height: 200%;
    border-radius: 6px;
    transform: scale(0.5);
  }
}

@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
  .btn--outline::after {
    width: 300%;
    height: 300%;
    border-radius: 9px;
    transform: scale(0.33333333);
  }
}

.btn--primary {
  color: #fff;
  background-color: #7266BA;
}

.btn--primary:active, .btn--primary.is-active {
  background-color: #675ca7;
}

.btn--primary[disabled], .btn--primary[disabled]:active, .btn--primary[disabled].is-active, .btn--primary.is-disabled, .btn--primary.is-disabled:active, .btn--primary.is-disabled.is-active,
fieldset[disabled] .btn--primary,
fieldset[disabled] .btn--primary:active,
fieldset[disabled] .btn--primary.is-active {
  opacity: .65;
  cursor: not-allowed;
  background-color: #aaa3d6;
}

.btn--primary.is-loading {
  color: transparent;
}

.btn--primary.is-loading:before {
  border-left-color: #fff !important;
  border-bottom-color: #fff !important;
}

.btn--primary.is-loading:focus, .btn--primary.is-loading:active, .btn--primary.is-loading:hover {
  color: transparent;
}

.btn--danger {
  color: #fff;
  background-color: #E74C3C;
}

.btn--danger:active, .btn--danger.is-active {
  background-color: #d04436;
}

.btn--danger[disabled], .btn--danger[disabled]:active, .btn--danger[disabled].is-active, .btn--danger.is-disabled, .btn--danger.is-disabled:active, .btn--danger.is-disabled.is-active,
fieldset[disabled] .btn--danger,
fieldset[disabled] .btn--danger:active,
fieldset[disabled] .btn--danger.is-active {
  opacity: .65;
  cursor: not-allowed;
  background-color: #f1948a;
}

.btn--danger.is-loading {
  color: transparent;
}

.btn--danger.is-loading:before {
  border-left-color: #fff !important;
  border-bottom-color: #fff !important;
}

.btn--danger.is-loading:focus, .btn--danger.is-loading:active, .btn--danger.is-loading:hover {
  color: transparent;
}

.btn--info {
  color: #fff;
