.Cell {
  position: relative;
  padding-left: 15px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}

.Cell:first-child > .Cell__body::before {
  display: none;
}

/* Elements
---------------------------------*/
.Cell__body {
  position: relative;
  color: inherit;
  display: flex;
  align-items: stretch;
  min-height: 44px;
  padding-right: 15px;
}

.Cell__body::before {
  content: '';
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  right: 0;
  bottom: auto;
  height: 1px;
  background-color: #ddd;
  transform-origin: 50% 0;
  z-index: 200;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
  .Cell__body::before {
    transform: scaleY(0.5);
  }
}

@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
  .Cell__body::before {
    transform: scaleY(0.33333333);
  }
}

.Cell__media {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
}

.Cell__media + .Cell__inner {
  margin-left: 8px;
}

.Cell__media i + i,
.Cell__media img + img {
  margin-left: 4px;
}

.Cell__media img {
  vertical-align: bottom;
}

.Cell__inner {
  flex: 1;
  display: flex;
  color: #555;
  font-size: 17px;
  align-items: center;
}

.Cell__inner + .Cell__media {
  margin-left: 8px;
}

.Cell__content {
  flex: 1;
}

.Cell__after {
  padding-left: 4px;
  color: #616161;
  display: flex;
  align-items: center;
}

/* Modifiers
---------------------------------*/
.Cell--link .Cell__body {
  padding-right: 35px;
  background-image: url(arrow-right.svg);
  background-position: calc(100% - 15px) center;
  background-size: auto 13px;
  background-repeat: no-repeat;
}

.Cell--link:active,
.Cell.watch-active:active {
  background-color: #ddd;
}

.Cell--link:active > .Cell__body::before,
.Cell--link:active + .Cell > .Cell__body::before,
.Cell.watch-active:active > .Cell__body::before,
.Cell.watch-active:active + .Cell > .Cell__body::before {
  display: none;
}

/*
|-------------------------------------------------------------------------------
| #Message
|-------------------------------------------------------------------------------
|
|
*/
/* Block
---------------------------------*/
.Message {
  position: relative;
  text-align: center;
  padding-top: 45px;
  margin-bottom: 30px;
}

/* Elements
---------------------------------*/
.Message__media {
  margin: 0 0 15px;
}

.Message__icon {
  width: 100px;
  height: 100px;
}

.Message__title {
  color: #424242;
  font-weight: bolder;
  font-size: 20px;
  line-height: 1.2;
  margin: 0 0 8px;
}

.Message__content {
  color: #616161;
  font-size: 14px;
  margin-bottom: 20px;
}

/* Modifiers
---------------------------------*/
/*
|-------------------------------------------------------------------------------
| #Modal
|-------------------------------------------------------------------------------
|
|
*/
/* Block
---------------------------------*/
.Modal {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  justify-content: center;
  align-items: center;
  z-index: 900;
}

/* Elements
---------------------------------*/
.Modal__body {
  position: relative;
  max-width: 320px;
  width: 100%;
  margin-left: 10px;
  margin-right: 10px;
  border-radius: 3px;
  background-color: #fff;
  overflow: hidden;
}

.Modal__header {
  position: relative;
  padding: 15px;
}

.Modal__header + .Modal__content {
  padding-top: 0;
}

.Modal__title {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 1.2;
  font-weight: normal;
}

.Modal__content {
  padding: 15px;
  font-size: 14px;
  max-height: 75vh;
}

.Modal__footer {
  position: relative;
  display: flex;
}

.Modal__footer::before {
  content: '';
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  right: 0;
  bottom: auto;
  height: 1px;
  background-color: #ddd;
  transform-origin: 50% 0;
  z-index: 200;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
  .Modal__footer::before {
    transform: scaleY(0.5);
  }
}

@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
  .Modal__footer::before {
    transform: scaleY(0.33333333);
  }
}

.Modal__btn {
  position: relative;
  display: block;
  text-align: center;
  flex: 1;
  height: 45px;
  line-height: 45px;
  font-size: 16px;
}

.Modal__btn:not(:last-child)::after {
  content: '';
  position: absolute;
  display: block;
  left: auto;
  top: 0;
  right: 0;
  bottom: 0;
  width: 1px;
  background-color: #ddd;
  transform-origin: 100% 50%;
  z-index: 200;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
  .Modal__btn:not(:last-child)::after {
    transform: scaleX(0.5);
  }
}

@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
  .Modal__btn:not(:last-child)::after {
    transform: scaleX(0.33333333);
  }
}

.Modal__btn:active {
  background-color: #fff;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 800;
}

/* Modifiers
---------------------------------*/
.Modal--default.is-in .Modal__body {
  animation-name: fadeInUp;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--default.is-out .Modal__body {
  animation-name: fadeOutUp;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--top {
  align-items: flex-start;
}

.Modal--top.is-in .Modal__body {
  animation-name: fadeInDown;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--top.is-out .Modal__body {
  animation-name: fadeOutUp;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--top .Modal__body {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  border-radius: 0;
}

.Modal--bottom {
  align-items: flex-end;
}

.Modal--bottom.is-in .Modal__body {
  animation-name: fadeInUp;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--bottom.is-out .Modal__body {
  animation-name: fadeOutDown;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--bottom .Modal__body {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  border-radius: 0;
  max-height: calc( 100% - 30px);
}

.Modal--max.is-in .Modal__body {
  animation-name: zoomIn;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--max.is-out .Modal__body {
  animation-name: zoomOut;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--max .Modal__body {
  margin: 15px;
  max-width: 610px;
  width: 100%;
  max-height: calc(100vh - 30px);
}

.Modal--full.is-in .Modal__body {
  animation-name: zoomIn;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--full.is-out .Modal__body {
  animation-name: zoomOut;
  animation-duration: 300ms;
  animation-fill-mode: both;
}

.Modal--full .Modal__body {
  margin-left: 0;
  margin-right: 0;
  max-width: 100%;
  border-radius: 0;
  width: 100%;
  height: 100%;
  padding-bottom: 45px;
}

.Modal--full .Modal__footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.Modal--alert {
  text-align: center;
}

/*
|-------------------------------------------------------------------------------
| #NavBar
|-------------------------------------------------------------------------------
|
|
*/
/* Block
---------------------------------*/
.NavBar {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 44px;
  background-color: #fff;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  z-index: 600;
}

.NavBar::after {
  content: '';
  position: absolute;
  display: block;
  left: 0;
  top: auto;
  right: 0;
  bottom: 0;
  height: 1px;
  background-color: #ddd;
  transform-origin: 50% 100%;
  z-index: 200;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
  .NavBar::after {
    transform: scaleY(0.5);
  }
}

@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
  .NavBar::after {
    transform: scaleY(0.33333333);
  }
}

/* Elements
---------------------------------*/
.NavBar__left {
  display: flex;
  justify-content: flex-start;
}

.NavBar__left .NavBar__item:not(:last-child) {
  margin-right: 8px;
}

.NavBar__center {
  flex-shrink: 0;
}

.NavBar__right {
  display: flex;
  justify-content: flex-end;
}

.NavBar__right .NavBar__item:not(:first-child) {
  margin-left: 8px;
}

.NavBar > .NavBar__item {
  flex: 1;
  text-align: center;
}

/*
|-------------------------------------------------------------------------------
| #Nav
|-------------------------------------------------------------------------------
|
|
*/
/* Block
---------------------------------*/
.Nav {
  position: relative;
  display: flex;
  flex: 1;
  justify-content: space-around;
  align-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 500;
}

/* Elements
---------------------------------*/
.Nav__item {
  flex: 1;
  width: 1%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #616161;
  font-size: 16px;
  word-wrap: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.Nav__link {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: inherit;
}

.Nav__link.is-active,
.Nav__link.is-active .Nav__label,
.Nav__link.is-active .Nav__icon {
  color: #7266BA;
}

.Nav__label {
  position: relative;
  display: block;
  color: rgba(97, 97, 97, 0.8);
  font-size: 10px;
  line-height: 12px;
  word-wrap: normal;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.Nav__icon {
  position: relative;
  width: 32px;
  height: 32px;
  font-size: 28px;
  display: flex;
  color: #616161;
  justify-content: center;
  align-items: center;
}

/* Modifiers
---------------------------------*/
.Nav--tabs {
  align-self: stretch;
}

.Nav--tabs .Nav__link {
  align-self: stretch;
}

.Nav--tabs .Nav__link.is-active {
  box-shadow: inset 0 -2px 0 0 #7266BA;
}

.Nav--segmented {
  display: inline-flex;
  justify-content: center;
}

.Nav--segmented.is-justify .Nav__item {
  flex: 1;
  width: 1%;
}

.Nav--segmented .Nav__link {
  font-size: 13px;
  line-height: 1;
  padding: 8px 16px;
}

.Nav--segmented .Nav__link:active, .Nav--segmented .Nav__link.is-active {
  color: #fff;
  background-color: #7266BA;
}

.Nav--segmented .Nav__link:active .Nav__link,
.Nav--segmented .Nav__link:active .Nav__label,
.Nav--segmented .Nav__link:active .Nav__icon, .Nav--segmented .Nav__link.is-active .Nav__link,
.Nav--segmented .Nav__link.is-active .Nav__label,
.Nav--segmented .Nav__link.is-active .Nav__icon {
  color: #fff;
}

.Nav--segmented .Nav__item {
  flex: none;
  width: auto;
  color: #7266BA;
  border: 1px solid #7266BA;
}

.Nav--segmented .Nav__item:not(:last-child) {
  border-right: 0;
}

.Nav--segmented .Nav__item:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}

.Nav--segmented .Nav__item:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

/* Misc
---------------------------------*/
.tab-content .tab-pane {
  display: none;
}

.tab-content .tab-pane.is-active {
  display: block;
}

/*
|-------------------------------------------------------------------------------
| #Notice
|-------------------------------------------------------------------------------
|
|
*/
/* Block
---------------------------------*/
.Notice {
  position: relative;
  border-radius: 3px;
  padding: 10px;
  margin-bottom: 1rem;
  color: #555;
  border: 1px solid #ddd;
  background-color: #fff;
}

.Notice.has-close {
  padding-right: 30px;
}

/* Elements
---------------------------------*/
.Notice__close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 16px;
  line-height: 1;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.3);
}

.Notice--danger {
  color: #75160e;
  border-color: #fad7d4;
  background-color: #fad7d4;
}

.Notice--info {
  color: #005899;
  border-color: #e5f4ff;
  background-color: #e5f4ff;
}

.Notice--success {
  color: #28672c;
  border-color: #e5f5e6;
  background-color: #e5f5e6;
}

.Notice--warning {
  color: #806400;
  border-color: #fff4cc;
  background-color: #fff4cc;
}

.Notice--sharp {
  border-radius: 0;
}

/*
|-------------------------------------------------------------------------------
| #Notify
|-------------------------------------------------------------------------------
|
|
*/
/* Block
---------------------------------*/
.Notify {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: transparent;
  z-index: 1000;
  overflow: hidden;
}

/* Elements
---------------------------------*/
.Notify__icon {
  display: block;
  width: 20px;
  height: 20px;
  margin: 0 auto 8px auto;
}

.Notify__icon[class^="sicon-"] {
  background-color: #fff;
}

.Notify__content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-width: 60px;
  min-height: 60px;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  background-color: rgba(0, 0, 0, 0.6);
  font-size: 12px;
  color: #fff;
  line-height: 1.2;
  text-align: center;
}

/* Modifiers
---------------------------------*/
.Notify--lg .Notify__icon {
  width: 40px;
  height: 40px;
  margin-bottom: 12px;
}

.Notify--lg .Notify__content {
  width: 160px;
  height: 120px;
  padding: 15px 20px;
  font-size: 16px;
}

/*
|-------------------------------------------------------------------------------
| #Progress
|-------------------------------------------------------------------------------
|
|
*/
/* 1.0 Progress Bar
---------------------------------*/
.Progress {
  position: relative;
  display: block;
  height: 3px;
  border-radius: 4px;
  background-color: #f3f7f9;
}

.Progress__bar {
  position: relative;
  height: 100%;
  border-radius: 4px;
  background-color: #4CD964;
}

.Progress__tip {
  position: absolute;
  right: 0;
  font-size: 12px;
  bottom: 100%;
}

/* 2.0 - Step Progress
---------------------------------*/
.Step {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: space-between;
}

.Step__item {
  position: relative;
  display: flex;
  justify-content: flex-end;
}

.Step__item:first-child {
  justify-content: flex-start;
}

.Step__item.is-active ~ .Step__item:before {
  background-color: #ddd;
}

.Step__item.is-active ~ .Step__item .Step__tip {
  background-color: #ddd;
}

.Step__item:nth-child(n+2) {
  flex: 1;
}

.Step__item:nth-child(n+2):before {
  content: '';
  display: block;
  position: absolute;
  left: 5px;
  top: 50%;
  margin-top: -1px;
  right: 25px;
  height: 2px;
  background-color: #7266BA;
}

.Step__tip {
  display: block;
  flex-shrink: 0;
  text-align: center;
  font-size: 12px;
  line-height: 20px;
  color: #fff;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background-color: #7266BA;
}

/*
|-------------------------------------------------------------------------------
| #Source
|-------------------------------------------------------------------------------
|
|
*/
/* Block
---------------------------------*/
.Source {
  display: inline-flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.2;
  color: #616161;
}

/* Elements
---------------------------------*/
.Source__left {
  margin-right: .5em;
}

.Source__left img {
  vertical-align: bottom;
}

.Source__title {
  color: #424242;
  font-size: .9em;
  margin: 0;
}

.Source__meta {
  font-size: .8em;
  color: #616161;
}

/*
|-------------------------------------------------------------------------------
| #TabBar
|-------------------------------------------------------------------------------
|
|
*/
.TabBar {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-wrap: nowrap;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  background-color: rgba(255, 255, 255, 0.9);
  -webkit-backdrop-filter: blur(2px);
  z-index: 700;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}

.TabBar::before {
  content: '';
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  right: 0;
  bottom: auto;
  height: 1px;
  background-color: #ddd;
  transform-origin: 50% 0;
  z-index: 200;
}

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
  .TabBar::before {
    transform: scaleY(0.5);
  }
}

@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {
  .TabBar::before {
    transform: scaleY(0.33333333);
  }
}

.TabBar ~ .l-main .l-main__inner {
  padding-bottom: 50px;
}

.TabBar.is-relative {
  position: relative;
}

.TabBar__inner {
  order: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 640px;
  width: 100%;
}

.TabBar__left {
  order: 1;
  flex: 1;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  overflow: hidden;
}

.TabBar__right {
  order: 3;
  flex: 1;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  overflow: hidden;
}

[class*="a-"] {
  animation-delay: 0;
  animation-duration: .3s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
}

@keyframes spin-around {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.a-fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.a-fadeOut {
  animation-name: fadeOut;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 30%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeOutUp {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(0, -30%, 0);
  }
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -30%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: translate3d(0, 30%, 0);
  }
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale(0.6);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes zoomOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    transform: scale(0.6);
  }
}

